import { useState, useEffect } from 'react';

// Mock types matching DB schema roughly
export interface HorizonData {
    payPeriods: {
        id: string;
        start_date: string;
        end_date: string;
        total_income: number;
        is_current: boolean;
    }[];
    allocationBuckets: {
        id: string;
        name: string;
        target_amount: number;
        current_amount: number;
        color_hex: string;
    }[];
    sentimentLogs: {
        id: string;
        logged_at: string;
        score: number; // 1-5 or similar
        note: string;
    }[];
}

export const useHorizon = () => {
    const [data, setData] = useState<HorizonData | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        // Simulate API delay for "Empathetic UX"
        const timer = setTimeout(() => {
            setData({
                payPeriods: [
                    {
                        id: '1',
                        start_date: '2023-10-01',
                        end_date: '2023-10-07',
                        total_income: 1500,
                        is_current: true,
                    },
                    {
                        id: '2',
                        start_date: '2023-10-08',
                        end_date: '2023-10-14',
                        total_income: 1500,
                        is_current: false,
                    },
                    {
                        id: '3',
                        start_date: '2023-10-15',
                        end_date: '2023-10-21',
                        total_income: 1500,
                        is_current: false,
                    },
                ],
                allocationBuckets: [
                    {
                        id: '1',
                        name: 'Emergency Fund',
                        target_amount: 10000,
                        current_amount: 4500,
                        color_hex: '#4A5D4E',
                    },
                    {
                        id: '2',
                        name: 'Vacation',
                        target_amount: 2000,
                        current_amount: 800,
                        color_hex: '#E9F0E9',
                    },
                    {
                        id: '3',
                        name: 'New Laptop',
                        target_amount: 1500,
                        current_amount: 1200,
                        color_hex: '#4A5D4E',
                    },
                ],
                sentimentLogs: [
                    {
                        id: '1',
                        logged_at: '2023-10-02T10:00:00Z',
                        score: 4,
                        note: 'Feeling good about savings',
                    },
                ],
            });
            setLoading(false);
        }, 1500); // 1.5s delay

        return () => clearTimeout(timer);
    }, []);

    return { data, loading };
};
