import React from 'react';
import { Card } from '../ui/Card';
import { ProgressBar } from '../ui/ProgressBar';
import { HorizonData } from '../../hooks/useHorizon';

interface BucketAllocationsProps {
    buckets: HorizonData['allocationBuckets'];
}

export const BucketAllocations = ({ buckets }: BucketAllocationsProps) => {
    const totalTarget = buckets.reduce((acc, b) => acc + b.target_amount, 0);
    const totalCurrent = buckets.reduce((acc, b) => acc + b.current_amount, 0);

    return (
        <Card title="Bucket Allocations" className="h-full">
            <div className="space-y-6">
                <div className="flex justify-between items-end mb-2">
                    <div>
                        <p className="text-sm text-gray-500">Total Saved</p>
                        <p className="text-2xl font-bold text-text-charcoal">${totalCurrent.toLocaleString()}</p>
                    </div>
                    <div className="text-right">
                        <p className="text-sm text-gray-500">Goal</p>
                        <p className="font-medium text-text-charcoal">${totalTarget.toLocaleString()}</p>
                    </div>
                </div>

                <div className="space-y-5">
                    {buckets.map((bucket) => (
                        <div key={bucket.id}>
                            <div className="flex justify-between text-sm mb-2">
                                <span className="font-medium">{bucket.name}</span>
                                <span className="text-gray-500">
                                    ${bucket.current_amount.toLocaleString()} / ${bucket.target_amount.toLocaleString()}
                                </span>
                            </div>
                            {/* Progress Bar (Styles handled in ProgressBar component or inline here if needed) */}
                            {/* User requested: Track h-2 w-full bg-[#E9F0E9] rounded-full. Fill h-full bg-[#4A5D4E] rounded-full. */}
                            {/* I updated the ProgressBar component to match this, so using it here is correct. */}
                            <ProgressBar
                                value={bucket.current_amount}
                                max={bucket.target_amount}
                            />
                        </div>
                    ))}
                </div>

                <div className="pt-4 border-t border-[#F2F1ED] mt-4">
                    <button className="text-[#4A5D4E] text-sm font-medium hover:underline">
                        Manage Buckets
                    </button>
                </div>
            </div>
        </Card>
    );
};
